@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&display=swap);
*{box-sizing:border-box;margin:0;padding:0;font-size:16px;font-family:inherit;color:#363636}html{font-family:"Open Sans", sans-serif}h1{margin-top:36px;padding-top:0;text-align:center;font-size:36px;font-weight:bold;color:#45852D}a{text-decoration:none}.container{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;max-width:1184px;margin:0 auto;padding:0;overflow:auto}.cards{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-flex-wrap:wrap;flex-wrap:wrap;grid-gap:38px 22px;gap:38px 22px;margin-top:45px}.button-see-more{width:150px;height:50px;margin:60px;padding-right:10px;-webkit-align-self:center;align-self:center;outline:none;background-color:#FBFBFB;border:1px solid #D8D8D8;border-radius:25px;cursor:pointer;font-weight:bold;letter-spacing:-0.3px;transition:all 0.3s ease-out;position:relative}.button-see-more:hover{-webkit-transform:scale(1.05);transform:scale(1.05)}.button-see-more__icon{position:absolute;right:28px;top:16px}.mx-2{margin:0 1.5rem}@media (max-width: 530px){.cards{margin:10px 0 0 0}.button-see-more{margin:20px}}

.filter{margin-top:45px;-webkit-align-self:left;align-self:left}.filter__title{font-weight:bold;letter-spacing:-0.3px}.filter__input{width:418px;height:50px;margin-left:15px;padding:18px;border:1px solid #D8D8D8;border-radius:25px;outline:none}@media (max-width: 530px){.filter{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;margin:25px auto 10px}.filter__title{margin-left:15px}.filter__input{width:300px;margin:10px}}

.card{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;width:380px;height:380px;border:1px solid #D8D8D8;position:relative}.card__tag{position:absolute;top:197px;right:0;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;width:134px;height:30px;color:#FFFFFF;font-size:12px;font-weight:bold}.card__tag.IndependentLiving{background-color:#006F79}.card__tag.SupportAvailable{width:204px;background-color:#EC6608}.card__details{padding:20px;padding-bottom:17px;background-color:#F8F8F8}.card__title{font-size:20px}.card__address{margin-top:3px}.card__price{margin-top:15px}.card__price-value{font-weight:bold}.card__extra{font-size:14px;margin-top:6px}@media (max-width: 430px){.card{width:300px}.card__tag{top:149px}}

